#navigation {
  visibility: hidden;
  opacity: 0;
  position: absolute;
  transform: translateY(-5px);
  transition: all 0.15s cubic-bezier(0.215, 0.610, 0.355, 1);
  cursor: default;
  background: white;
  top: 80%;
  z-index: 5;

  .no-right {
    position: absolute;
  }

  @include breakpoint(md-down) {
    &:not(.no-right) {
      right: 20px;
    }
  }

  .submenu {
    box-shadow: 0px 4px 9px rgba(0, 0, 0, 0.15);
    overflow: hidden;
    width: auto;

    .col-md-3 {
      @include breakpoint(md) {
        max-width: 23%;
      }
    }

    &.auto-width {
      @include breakpoint(md) {
        width: auto;
      }
    }

    .announcement {
      display: flex;
      flex-direction: column;
      color: white;
      margin-left: auto;

      h2 {
        font-size: 14px;
        line-height: 18px;
        font-weight: bold;
        letter-spacing: 0.005em;
      }

      h1 {
        font-weight: 500;
        font-size: 24px;
        line-height: 30px;
        letter-spacing: -0.3px;
        max-width: 195px;
        margin-top: 48px;
      }

      .btn {
        border: none;
        color: black !important;
        align-self: baseline;
        margin-top: auto;
      }
    }

    .column {
      transition: all .15s ease;

      &.fade {
        opacity: .3;
      }

      .grey {
        fill: #000000;

        &:hover {
          background: #F4F4F4;
        }
      }

      .pink {
        fill: #FFE9F0;

        &:hover {
          background: #FAF3F5;
        }
      }

      .green {
        fill: #D6EBE4;

        &:hover {
          background: #F2F8F6;
        }
      }

      .yellow {
        fill: #FAECDB;

        &:hover {
          background: #FAF8F5;
        }
      }

      h4,
      p {
        margin: 0;
        padding: 0;
      }

      padding: 32px;

      &.main-column {
        min-width: 265px;
        background: #FCFCFC;

        h4 {
          font-weight: bold;
        }
      }

      ul {
        padding: 0;
        margin: 0 auto;
        position: relative;
        list-style: none;

        li {
          float: none;
          margin-right: 0;
        }
      }

      h4 {
        margin: 0;
        font-size: 14px;
        line-height: 18px;
        vertical-align: middle;
        line-height: normal;
      }

      .title {
        padding-bottom: 25px;
        display: flex;
        align-items: center;
        flex-direction: row;
        border-bottom: 1px solid rgba(0, 0, 0, 0.10);

        .tag {
          background-color: #F2F2F2;
          font-size: 11px;
          line-height: 16px;
          border-radius: 20px;
          padding: 2px 8px;
          letter-spacing: 0.01em;
          font-weight: normal;
          margin-left: 8px;
          line-height: normal;
        }

        h4 {
          font-weight: bold;
        }

        svg {
          margin-top: 2px;
          margin-right: 5px;
        }
      }

      .pages {

        .more-link {
          padding: 14px 8px;
          opacity: .3;
          transition: all .2s ease;

          span {
            transition: all .2s ease;
          }

          &:hover {
            opacity: .6;
            cursor: pointer;
            margin-left: 2px;

            span {
              padding-left: 5px;
            }

          }
        }

        .page {
          &:nth-child(1) {
            margin-top: 5px;
          }

          .inner {
            border-radius: 5px;
            padding-bottom: 30px;
            transition: all .2s ease;

            &:hover {
              cursor: pointer;
            }

            padding: 14px 8px;
          }

          p {
            font-size: 11px;
            line-height: 16px;
            opacity: .6;
            margin-top: .3125rem;
          }
        }
      }
    }
  }
}

#header {
  height: $mobilenavheight;
  padding-left: 0.5rem;
  padding-right: 1rem;
  margin: 0;

  #menu-icon {
    &.is-active {
      padding-right: 17px;
    }
  }

  @include breakpoint(sm) {
    height: $navheight;
    padding-left: 0;
    padding-right: 0;
    margin-left: -0.5rem;
    margin-right: -0.5rem;
  }

  .logo {
    display: block;
    width: 2.25rem;

    svg {
      z-index: 12;
      fill: $dark;
      position: relative;
      display: block;
    }
  }

  button {
    outline: none !important;
    z-index: 12;
    position: relative;
    display: block;
    margin-left: auto;
  }

  ul {
    list-style-type: none;
    margin: 0;
    padding: 0;
    background-color: transparent;

    &.right {
      margin-left: auto;
    }
  }

  li {
    float: left;
    margin-right: 2rem;

    @include breakpoint(md) {
      margin-right: 3rem;
    }

    cursor: pointer;

    a {
      transition: all 0.12s;
    }

    &:hover>a {
      opacity: 0.5;
    }

    &.cta {

      a {
        padding: 16px 24px !important;
        background-color: black;
        opacity: 1;
      }

      line-height: 2.375rem;
    }

    a:not(.btn) {
      text-align: left;
      font-size: 1rem;
      line-height: 2.375rem;
      color: $dark;
      text-decoration: none;
    }

    &.has-submenu {
      &:hover {
        #navigation {
          visibility: visible;
          opacity: 1;
          transform: translateY(6px);
        }
      }
    }
  }

  .col-xs-7 li {
    line-height: 3.125rem;
  }
}

#menu-overlay {
  position: fixed;
  z-index: 11;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: white;
  display: none;
  opacity: 0;
  transition: all 0.2s ease;

  .container,
  .row {
    height: 100%;
  }

  ul {
    margin-top: 58px;
    padding: 0px 30px;
    list-style: none;
    width: 100%;

    li {
      margin-top: 0.7rem;
      margin-bottom: 0.7rem;
      border-bottom: 1px solid rgba(0, 0, 0, 0.25);
      cursor: pointer;

      @media screen and (max-height: 560px) {
        margin-top: 0.2rem;
        margin-bottom: 0.2rem;
      }

      a {
        padding: 12.5px 0;
        color: black !important;
        text-decoration: none;
        font-size: 1.625rem;
        line-height: 2.375rem;
        display: block;
        box-sizing: border-box;
        width: 100%;
      }

      &.dropdown-item {
        margin: 0;

        .dropdown {
          display: flex;
          align-items: center;
          flex-direction: row;

          .arrow-ico {
            margin-left: auto;
            transition: all .15s cubic-bezier(0.215, 0.610, 0.355, 1);

            &.open {
              transform: rotate(180deg);
            }
          }
        }

        .content {
          ul {
            padding: 0;
            margin: 0 0 16px 0;

            li {
              border: none;
              margin: 0;

              &:not(:first-child) {
                padding-bottom: 12px;
              }

              p {
                padding: 0;
                margin: 0;
                font-size: 11px;
                line-height: 16px;
                letter-spacing: 0.01em;
                opacity: .75;
              }

              a {
                font-size: 16px;
                line-height: 26px;
                font-weight: 400;
                padding-top: 0;
                padding-bottom: 0;
                margin: 0;
              }
            }
          }

          .content-title {
            font-weight: bold;
            padding-bottom: 6px;

            svg {
              margin-right: 5px;
            }
          }

          display: none;
          transition: all .15s cubic-bezier(0.215, 0.610, 0.355, 1);
          animation: toggle forwards 0.2s ease-in-out;
          opacity: 0;

          &.show {
            display: block;
            opacity: 1;
          }

        }
      }

      @keyframes toggle {
        from {
          opacity: 0;
          transform: scaleY(0);
          transform: translateY(-5px);
        }

        to {
          opacity: 1;
          transform: translateY(0px);
          transform: scaleY(1);
        }
      }

      @keyframes menuOverlay {
        from {
          opacity: 0;
          transform: translateY(20px);
        }

        to {
          opacity: 1;
          transform: translateY(0px);
        }
      }

      &.has-submenu {
        margin-bottom: 0;

        a {
          font-weight: bold;
        }
      }

      &.is-submenu {
        margin-top: 0;
        margin-bottom: 0;
      }
    }
  }

  div.cta {
    bottom: 10px;
    left: 30px;
    width: calc(100% - 60px);
    margin: auto;
    margin-top: auto;
    margin-bottom: 25px;
    
    .no-background {
      background: none;
      color: black;
      border: none;
    }

    a {
      display: block;
      border-color: $dark;

      span {
        font-weight: 400;
      }
    }
  }
}

.with-menu {
  overflow-y: hidden;

  #menu-overlay {
    animation: menuOverlay forwards 0.15s cubic-bezier(0.25, 0.46, 0.45, 0.94);
    display: block;
    overflow-y: scroll;
    overflow-x: none !important;
    padding-right: 17px;
    /* Increase/decrease this value for cross-browser compatibility */
    box-sizing: content-box;
    /* So the width will be 100% + 17px */
    opacity: 1;
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: -17px;
    /* Increase/Decrease this value for cross-browser compatibility */
  }

  #header {
    .logo svg {
      fill: rgb(0, 0, 0);
    }

    .hamburger-inner,
    .hamburger-inner::before,
    .hamburger-inner::after {
      background-color: rgb(0, 0, 0);
    }
  }

  #header-container {
    z-index: 200;
    background: white;
  }
}

#header-container {
  position: fixed;
  width: 100%;
  left: 0;
  top: 0;
  background: white;
  z-index: 200;
}